/*=========================================================
# top
=========================================================*/
/* ページネーション
--------------------------------------------------*/
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  position: absolute;
  width: inherit;
  left: -2rem;
  bottom: 1.5rem;
}
@media screen and (min-width: 768px) {
  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    left: -2.5rem;
    bottom: 0.875rem;
  }
}

.swiper-pagination-bullet.swiper-pagination-bullet-active {
  background-color: #1391e6;
}

.swiper-pagination {
  transform: rotate(90deg);
}

/* 矢印
--------------------------------------------------*/
.swiper-button-prev::after,
.swiper-button-next::after {
  bottom: 0;
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
}

.swiper-button-prev::after {
  background-image: url(../images/common/mv-arrow-left.png);
  width: 3.125rem;
  height: 3.125rem;
  left: -2.1875rem;
  background-repeat: no-repeat;
  background-size: contain;
}
@media screen and (min-width: 768px) {
  .swiper-button-prev::after {
    width: 4.125rem;
    height: 4.125rem;
    left: -2.625rem;
  }
}

/* 次への矢印カスタマイズ */
.swiper-button-next::after {
  background-image: url(../images/common/mv-arrow-right.png);
  width: 3.125rem;
  height: 3.125rem;
  right: -2.1875rem;
  background-repeat: no-repeat;
  background-size: contain;
}
@media screen and (min-width: 768px) {
  .swiper-button-next::after {
    width: 4.125rem;
    height: 4.125rem;
    right: -2.75rem;
  }
}

